<template>
    <el-dialog :title="titleShow==true?'Add':'Edit'"  v-model="dialogForm" class="updatePwd">
        <el-form :model="form">
            <el-form-item label="CompanyCode:" :label-width="formLabelWidth"  :required="true">
                <el-input v-model.trim="form.CompanyCode" auto-complete="off" size="small"></el-input>
            </el-form-item>
            <el-form-item label="CompanyName:" :label-width="formLabelWidth"  :required="true">
                <el-input v-model.trim="form.CompanyName" auto-complete="off" size="medium"></el-input>
            </el-form-item>

            <el-form-item label="CurrencyCode:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.CurrencyCode" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="EmprTaxFileNum:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.EmprTaxFileNum" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="Fax:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.Fax" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="PayDay:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.PayDay" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="PeriodType:" :label-width="formLabelWidth">
                <el-select
                        size="small"
                        v-model="form.PeriodType"
                        placeholder="Please choose"
                >
                    <el-option
                            v-for="(item ,$key) in type"
                            :label="item.name"
                            :value="item.value"
                            :key="$key">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="Phone:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.Phone" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="PostalAddress:" :label-width="formLabelWidth">
                <el-input v-model.trim="form.PostalAddress" auto-complete="off"></el-input>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogForm = false">Cancel</el-button>
            <el-button type="primary" @click="submit()">Submit</el-button>
        </div>
    </el-dialog>
</template>

<style scope="" lang="less" type="text/scss">
.updatePwd{
    .el-dialog__header{
        padding:30px;
        .el-dialog__headerbtn{
            width:50px;
            height:50px;
            margin:-30px -30px 0 0;
        }
    }
    .el-dialog__body{
        padding:30px 50px 0 0;
    }
}
</style>

<script>
import QuRequest from '../../core/QuRequest'

const quRequest = new QuRequest()

export default {

    data() {
        return {
            type: [
                { 'name': 'Daily', 'value': '0'},
                { 'name': 'Weekly','value': '1'},
                { 'name': 'Monthly', 'value': '2'},
                { 'name': 'Annual', 'value': '3'}
            ],
            Gender:[
                { 'name': 'man', 'value': true},
                { 'name': 'women','value': false},
            ],
            IsPartime:[
                { 'name': 'yes', 'value': true},
                { 'name': 'no','value': false},
            ],
            IsActive:[
                { 'name': 'yes', 'value': true},
                { 'name': 'no','value': false},
            ],
            MaritalStatus:[
                { 'name': 'no','value': 0},
                { 'name': 'yes', 'value': 1},
            ],
            titleShow:false,
            dialogForm: false,
            formLabelWidth: '150px',
            disabled:true,
            isShow:true,
            form:{
                'CompanyCode':'',
                'PostalAddress': '',
                'Phone':'',
                'PeriodType':'1',
                'PayDay':'',
                'Fax':'',
                'EmprTaxFileNum':'',
                'CurrencyCode':'',
                'Address':'',
                'CompanyName':'',
            }
        }
    },
    mounted() {

    },
    methods: {
        formatDate(dateStr){
            let date = new Date(dateStr);
            let y = date.getFullYear();
            let m = date.getMonth() + 1;
            let d = date.getDate();
            return [y, this.addZero(m), this.addZero(d)].join('/');
        },
        addZero: function (num) {
            return (num > 9 ? '' : '0') + num
        },
        showDialog(item) {
            if(item){
                this.isShow = false;
                this.form.CompanyCode= item.CompanyCode;
                this.form.CompanyName =item.CompanyName;
                this.form.Address=item.Address;
                this.form.CurrencyCode =item.CurrencyCode;
                this.form.EmprTaxFileNum= item.EmprTaxFileNum;
                this.form.Fax =item.Fax;
                this.form.PayDay=item.PayDay;
                this.form.PeriodType =item.PeriodType;
                this.form.Phone= item.Phone;
                this.form.PostalAddress =item.PostalAddress;
                this.titleShow = false;
            }else{
                this.titleShow = true;
                this.form.CompanyCode= '';
                this.form.CompanyName ='';
                this.form.Address='';
                this.form.CurrencyCode ='';
                this.form.EmprTaxFileNum= '';
                this.form.Fax ='';
                this.form.PayDay='';
                this.form.PeriodType ='';
                this.form.Phone= '';
                this.form.PostalAddress ='';
            }

            this.dialogForm = true;
        },
        submit(){
            quRequest.send(quRequest.Apis.qutuiba.member.update,{'json' :'{update:{Company:['+JSON.stringify(this.form)+']}}'}
            ).then(res => {
                this.$message.success("Submit Success！");
                this.dialogForm = false;
                this.$emit('update')
            }).catch(err => {
                this.$message.error(err.message);
            }).fin(() => {
                this.loading = false;
            }).done();
        }
    }
}
</script>


